body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@charset "UTF-8";
/* OPTIONS */
* {
    box-sizing: border-box;
}

body {
    font-family: 'Lato', sans-serif;
    background-color: #fff;
    color: #A2ACB0;
}

.hidden {
    display: none;
}

/* STYLES */
.main {
    height: 100vh;
    width: 300px;
    display: block;
    margin: 50px auto;
    text-align: center;
    margin-bottom: 1em;
}

h1 {
    font-size: 36px;
    font-weight: 300;
    color: #343C3F;
    margin-bottom: 0;
}

.torso {
    display: grid;
    grid-template-rows: repeat(3 1fr [col-start]);
    grid-row-gap: 0.8em;
}

.torso div:last-of-type {
    box-shadow: 0px 2px 15px 0px rgba(0,0,0,0.75);
}

.stage {
    padding: 0.5em;
    border: 1px solid hsl(0,0%,80%);
    border-radius: 4px;
}

.phone-email input {
    width: 100%;
    height: 38px;
    border: 1px solid hsl(0,0%,80%);
    /* outline: 1px solid hsl(0,0%,80%); */
    outline: none;
    border-radius: 4px;
    cursor: default;
}

.phone-email input:focus,
.phone-email input:focus-within,
.phone-email input:active {
    border-color: #2684FF !important;
    box-shadow: 0 0 0 1px #2684FF !important;
    outline: 1px solid #2684FF;
}

button[type=button]{
    padding: 10px 30px;
    background-color: white;
    cursor: pointer;
    border: 1px solid gray;
    border-radius: 5px;
    font-size: 14px;
    text-shadow: 1px 1px #e3e3e3;

}
